@import url("https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700;800&family=Noto+Sans+KR:wght@400;500&family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;0,1000;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900;1,1000&family=Pacifico&display=swap");

@tailwind base;
@tailwind components;
@tailwind utilities;

.page {
  @apply bg-lime-50 flex flex-col my-2;
}
.card {
  @apply w-11/12 bg-white rounded overflow-hidden shadow-md relative;
}

.centerPage {
  @apply flex flex-col items-center my-2;
}

.centerItem {
  @apply flex justify-center items-center;
}

.submenu {
  @apply text-xl font-bold ml-5;
}
